@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap");
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
}

html {
  height: 100%;
  font-size: 14px;
  font-family: "Rubik", sans-serif;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
}

body {
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #000000;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

input[type=number] {
  -moz-appearance: textfield;
}

.calculator {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 25px;
}
@media (min-width: 768px) {
  .calculator {
    width: 740px;
    margin: 25px auto 0;
    padding: 50px 25px 25px;
    border-radius: 15px;
    box-shadow: 0 10px 40px 0 #cdcfd4;
  }
}
@media (min-width: 1200px) {
  .calculator {
    width: 1180px;
    margin: 58px auto 0;
    padding: 77px 75px 75px;
  }
}

.calculator__title {
  font-weight: normal;
  margin: 0 0 25px 0;
  font-size: 22px;
  line-height: 22px;
}
@media (min-width: 768px) {
  .calculator__title {
    margin: 0 0 40px 0;
    font-size: 30px;
    line-height: 30px;
  }
}
@media (min-width: 1200px) {
  .calculator__title {
    margin: 0 0 57px 0;
    font-size: 38px;
    line-height: 38px;
  }
}

.calculator__form {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .calculator__form {
    display: grid;
    grid-template-columns: 625px 250px;
    column-gap: 100px;
  }
}

.calculator__wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .calculator__wrapper {
    margin-bottom: 40px;
  }
}
@media (min-width: 1200px) {
  .calculator__wrapper {
    margin-bottom: 0;
  }
}

.calculator__settings {
  display: flex;
  flex-direction: column;
  border: none;
}

.calculator__additional-text {
  margin: -8px 0 0;
  padding: 0;
  font-size: 8px;
  color: #adafb1;
}
@media (min-width: 768px) {
  .calculator__additional-text {
    font-size: 12px;
  }
}

.calculator__button {
  display: inline-block;
  width: 100%;
  padding: 17px;
  margin: 2px auto 18px;
  font-size: 16px;
  color: #ffffff;
  background-color: #67ac5b;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.calculator__button:hover {
  background-color: #528d47;
}
.calculator__button:active, .calculator__button:focus {
  background-color: #528d47;
  box-shadow: inset 0 0 0 1px #000000;
}
@media (min-width: 768px) {
  .calculator__button {
    width: 80%;
    margin: 2px auto 38px;
  }
}
@media (min-width: 1200px) {
  .calculator__button {
    width: 305px;
    margin: 2px 0 38px;
  }
}

.calculator__config-link {
  position: absolute;
  right: 25px;
  top: 25px;
  width: 40px;
  height: 40px;
  background-image: url("../img/gear.png");
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  transition: 1s;
}
.calculator__config-link:hover {
  transform: rotate(90deg);
  transition: 1s;
}
.calculator__config-link:active {
  transform: rotate(360deg);
  transition: 1s;
}
@media (min-width: 768px) {
  .calculator__config-link {
    right: 25px;
    top: 40px;
    width: 50px;
    height: 50px;
  }
}
@media (min-width: 1200px) {
  .calculator__config-link {
    right: 75px;
    top: 75px;
  }
}

.settings {
  padding: 0;
  margin: 0;
}

.settings__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .settings__wrapper {
    margin-bottom: 67px;
  }
}

.settings__title {
  display: inline-block;
  padding: 0;
  margin: 0 0 15px;
  font-size: 16px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .settings__title {
    margin: 0 0 0 15px;
    font-size: 18px;
    line-height: 18px;
  }
}
@media (min-width: 1200px) {
  .settings__title {
    margin-left: 0;
    font-size: 22px;
    line-height: 22px;
  }
}

.settings__container {
  margin: 0 auto;
}
@media (min-width: 390px) {
  .settings__container {
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .settings__container {
    margin-top: 4px;
    margin-right: 0;
  }
}

.settings__label {
  padding: 5px;
  font-size: 12px;
  border-radius: 6px 0 0 6px;
  border: 3px solid #cdcfd4;
  cursor: pointer;
}
@media (min-width: 768px) {
  .settings__label {
    padding: 10px 15px 11px;
    font-size: 14px;
    line-height: 14px;
  }
}
@media (min-width: 1200px) {
  .settings__label {
    font-size: 16px;
    line-height: 16px;
  }
}

.settings__label:first-of-type {
  border-right: 3px solid #67ac5b;
}
@media (min-width: 768px) {
  .settings__label:first-of-type {
    padding-left: 20px;
  }
}

.settings__label:nth-of-type(2) {
  margin-left: -5px;
  border-radius: 0 6px 6px 0;
  border-left: 1px solid #67ac5b;
}
@media (min-width: 768px) {
  .settings__label:nth-of-type(2) {
    padding-left: 16px;
  }
}

.settings__input:checked + .settings__label {
  border-color: #67ac5b;
}

.settings__input:hover + .settings__label {
  border-color: #bfc2c8;
}

.settings__input:checked:hover + .settings__label {
  border-color: #5b9e50;
}

.range {
  flex-wrap: wrap;
  position: relative;
  min-height: 48px;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .range {
    margin-bottom: 61px;
  }
}

.range__title {
  padding: 0;
  margin: 2px 0 0;
  font-size: 14px;
  line-height: 14px;
}
@media (min-width: 768px) {
  .range__title {
    margin: 2px 0 0 20px;
    font-size: 18px;
    line-height: 18px;
  }
}
@media (min-width: 1200px) {
  .range__title {
    margin-bottom: 6px;
    font-size: 22px;
    line-height: 22px;
  }
}

.range__number {
  width: 60px;
  margin-left: auto;
  max-height: 20px;
  font-size: 14px;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .range__number {
    width: 80px;
    max-height: 32px;
    font-size: 18px;
    line-height: 18px;
  }
}
@media (min-width: 1200px) {
  .range__number {
    width: 100px;
    margin-left: 333px;
    margin-top: -5px;
    font-size: 22px;
    line-height: 22px;
  }
}

.range__number::-webkit-outer-spin-button,
.range__number::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

.range__number:hover {
  color: #67ac5b;
}

.range__number:focus {
  outline: none;
  color: #487c3f;
}

.range__description {
  display: inline-block;
  vertical-align: middle;
  max-height: 16px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .range__description {
    max-height: 32px;
    font-size: 18px;
  }
}
@media (min-width: 1200px) {
  .range__description {
    font-size: 20px;
    line-height: 24px;
  }
}

.range__number:hover + .range__description {
  color: #67ac5b;
}

.range__number:focus + .range__description {
  outline: none;
  color: #487c3f;
}

.range__addition-info {
  position: absolute;
  bottom: -20px;
  right: 0;
  font-size: 12px;
  color: #adafb1;
}
.range__addition-info:first-of-type {
  left: 0;
  right: auto;
}
@media (min-width: 768px) {
  .range__addition-info {
    font-size: 14px;
    line-height: 14px;
  }
}
@media (min-width: 1200px) {
  .range__addition-info {
    font-size: 14px;
    line-height: 14px;
  }
}

.range__input {
  width: 100%;
  height: 4px;
  margin: 0;
  padding: 0;
  align-self: center;
  background-color: #ced0d4;
  background-image: linear-gradient(#67ac5b, #67ac5b);
  background-size: 13% 100%;
  background-repeat: no-repeat;
  appearance: none;
}

.range:last-of-type .range__input {
  background-size: 60% 100%;
}

.range:last-of-type .range__number {
  padding: 0 10px;
  width: 45px;
}

.range__input::-webkit-slider-thumb {
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #67ac5b;
  border: 1px solid #67ac5b;
  filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));
  appearance: none;
  cursor: pointer;
}
.range__input::-webkit-slider-thumb:hover {
  background-color: #5b9e50;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
}
.range__input::-webkit-slider-thumb:active {
  background-color: #5b9e50;
  filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  border-color: #000000;
}

.range__input::-moz-range-thumb {
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #67ac5b;
  border: 3px solid #67ac5b;
  filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));
  appearance: none;
  cursor: pointer;
}
.range__input::-moz-range-thumb:hover {
  background-color: #5b9e50;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
}
.range__input::-moz-range-thumb:active {
  background-color: #5b9e50;
  filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  border-color: #000000;
}

.range__input:focus {
  outline: none;
  background-image: linear-gradient(#528d47, #528d47);
}

.range__input:focus::-webkit-slider-thumb {
  background-color: #528d47;
  border-color: #000000;
}

.information {
  padding: 0;
  margin: 0 0 20px;
  border: none;
}
@media (min-width: 768px) {
  .information {
    margin: -10px 0 0;
  }
}

.information__wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .information__wrapper {
    margin-bottom: 0;
  }
}

.information__list {
  max-width: 200px;
  margin: 0 auto;
}
@media (min-width: 500px) {
  .information__list {
    max-width: 80%;
    display: grid;
    grid-template-columns: 45% 45%;
    column-gap: 10%;
  }
}
@media (min-width: 1200px) {
  .information__list {
    max-width: 100%;
    display: block;
  }
}

.information__title {
  margin-bottom: 12px;
  color: #adafb1;
}

.information__description {
  font-size: 40px;
  line-height: 40px;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .information__description {
    margin: 0 0 32px 35px;
  }
}
@media (min-width: 1200px) {
  .information__description {
    margin: 0 0 32px 0;
    font-size: 50px;
    line-height: 50px;
  }
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal--open {
  display: block;
}

.modal__popup {
  position: absolute;
  left: calc(50vw - 125px);
  top: calc(50vh - 75px);
  width: 250px;
  min-height: 150px;
  padding: 25px;
  border-radius: 15px;
  background-color: #ffffff;
  box-shadow: 0 0 50px 0 #cdcfd4;
}
@media (min-width: 768px) {
  .modal__popup {
    left: calc(50vw - 250px);
    top: calc(50vh - 125px);
    width: 500px;
    min-height: 250px;
  }
}
@media (min-width: 1200px) {
  .modal__popup {
    padding: 50px;
    width: 500px;
    min-height: 250px;
  }
}

.modal__description {
  width: 150px;
  position: absolute;
  left: calc(50% - 75px);
  top: 25px;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
}
@media (min-width: 768px) {
  .modal__description {
    top: 50px;
    left: calc(50% - 150px);
    width: 300px;
    font-size: 30px;
    line-height: 40px;
  }
}

.modal__button {
  position: absolute;
  left: calc(50% - 75px);
  bottom: -20px;
  width: 150px;
  padding: 7px;
  margin: 2px 0 40px;
  font-size: 14px;
  color: #ffffff;
  background-color: #67ac5b;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.modal__button:hover {
  background-color: #528d47;
}
.modal__button:active, .modal__button:focus {
  background-color: #528d47;
  box-shadow: inset 0 0 0 1px #000000;
}
@media (min-width: 768px) {
  .modal__button {
    bottom: 0;
    left: calc(50% - 100px);
    width: 200px;
    padding: 17px;
    font-size: 16px;
  }
}

.modal-open {
  height: 100vh;
  overflow: hidden;
}

.config {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  max-width: 100%;
  min-height: 500px;
  padding: 25px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: -10px 10px 35px 0 #cdcfd4;
  z-index: 1;
}
@media (min-width: 768px) {
  .config {
    top: 10px;
    width: auto;
  }
}

.config__title {
  width: 300px;
  padding: 0;
  margin: 0 auto 35px 0;
  font-weight: normal;
  font-size: 24px;
}

.config__wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.config__label {
  margin: 0 0 15px;
  padding: 0;
  font-size: 16px;
}

.config__input {
  outline: none;
  border-radius: 6px;
  border: 2px solid #cdcfd4;
}
.config__input::-webkit-outer-spin-button, .config__input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}
.config__input:hover {
  border-color: #67ac5b;
}
.config__input:active, .config__input:focus {
  border-color: #487c3f;
}

.config__link {
  display: inline-block;
  width: 100%;
  padding: 7px;
  margin: 15px 0 0;
  font-size: 16px;
  color: #ffffff;
  background-color: #67ac5b;
  border-radius: 5px;
  border: none;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}
.config__link:hover {
  background-color: #528d47;
}
.config__link:active, .config__link:focus {
  background-color: #528d47;
  box-shadow: inset 0 0 0 1px #000000;
}

.transition-fade {
  transition: 0.5s;
  opacity: 1;
}

html.is-animating .transition-fade {
  opacity: 0;
}

html.is-leaving .transition-fade {
  opacity: 0;
}